export default {
	data() {
		return {
			title: 'Hello',
			latitude: 39.909,
			longitude: 116.39742,
			covers: [{
				latitude: 39.909,
				longitude: 116.39742,
				iconPath: '../../../static/logo.png'
			}, {
				latitude: 39.90,
				longitude: 116.39,
				iconPath: '../../../static/logo.png'
			}],
			debugMsg: "随便塞点信息在这里"
		}
	},
	onReady() {
		let that = this;
		// https://uniapp.dcloud.io/api/location/map?id=createmapcontext
		// 据说可以获得当前位置
		// 但是我调用了没反应
		// 你们可以看看是有什么条件还是什么情况
		uni.getLocation({
			type: 'gcj02', //返回可以用于uni.openLocation的经纬度
			success: function(res) {
				that.latitude = res.latitude;
				that.longitude = res.longitude;
			}
		});
	},
	onLoad() {

	},
	methods: {
		clickMap(e) {
			console.log(e);
			this.getCenter();
		},
		getCenter() {
			// https://uniapp.dcloud.io/api/location/map?id=createmapcontext
			let appMap = uni.createMapContext("map-body", this);
			let that = this;
			// 没找到鼠标点击位置的经纬度怎么获取
			// 但是可以获取地图中心的经纬度
			appMap.getCenterLocation({
				success(res) {
					that.debugMsg = res.latitude + "," + res.longitude;
				}
			})
		},
	}
}
